import React, { Component } from 'react';
import {EmptyLayout} from '../layouts/empty_layout';
class Terminos extends Component {
  render() {
    return (
      <EmptyLayout>
      <main>       
        <section className="padding_10_0 margin_auto txt_align_justify" id={'avisoID'}>
            <h2 className="line_height_none">AVISO DE PRIVACIDAD</h2>
            <div>             
             <p className="txt_align_justify"> 
El presente Aviso de Privacidad (en adelante el “Aviso”) establece los términos y condiciones en virtud de los cuales la Fundación Internacional de Libre Expresión Motora y de Destrezas INTERLEM’D, identificado con NIT 900459129-7 y con domicilio principal en la Carrera 24 No. 23-05. Barrio el Salitre - Yopal Casanare, República de Colombia, realizará el tratamiento de sus datos personales.
</p>
<p>1.	TRATAMIENTO Y FINALIDAD:</p>
<p className="txt_align_justify">
El tratamiento que realizará la Fundación Internacional de Libre Expresión Motora y de Destrezas INTERLEM’D con la información personal será el siguiente:
La recolección, almacenamiento, uso, circulación, procesamiento de datos personales garantizando los lineamientos de seguridad y confidencialidad definidos en la Ley 1581 de 2012 y el Decreto 1377 de 2013, para:
Registrar la información de datos personales en las bases de datos y sistemas de información de INTERLEM’D. Ofrecer los programas corporativos de bienestar y planificar actividades institucionales, para el titular. Efectuar encuestas de satisfacción respecto de los bienes y servicios ofrecidos por INTERLEM’D. Efectuar las gestiones pertinentes para el desarrollo del objeto social de la fundación INTERLEM’D en lo que tiene que ver con el cumplimiento de los acuerdos, convenios y  contratos realizados con el Titular de la información.
</p>

<p>2.	DERECHOS DEL TITULAR:</p>
<p>Como titular de sus datos personales Usted tiene derecho a:</p>
<p>
<ul>
    <li>Acceder en forma gratuita a los datos proporcionados que hayan sido objeto de tratamiento.</li>
    <li>Conocer, actualizar y rectificar su información frente a datos parciales, inexactos, incompletos, fraccionados, que induzcan a error, o a aquellos cuyo tratamiento esté prohibido o no haya sido autorizado.</li>
    <li>Solicitar prueba de la autorización otorgada.</li>
    <li>Presentar ante la Superintendencia de Industria y Comercio (SIC) quejas por infracciones a lo dispuesto en la normatividad vigente.</li>
    <li>Revocar la autorización y/o solicitar la supresión del dato, siempre que no exista un deber legal o contractual que impida eliminarlos.</li>
    <li>Abstenerse de responder las preguntas sobre datos sensibles. Tendrá carácter facultativo las respuestas que versen sobre datos sensibles o sobre datos de las niñas y niños y adolescentes.</li>
</ul>
</p>
<p>3.	MECANISMOS PARA CONOCER LA POLÍTICA DE TRATAMIENTO</p>
<p>El Titular puede acceder a nuestra Política de Tratamiento de información, la cual se encuentra publicada en el siguinete link: <a href="#politicaID">Política</a>
</p>
            
            </div>
        </section>  

        <section className="grid_col_1 padding_10_0 margin_auto txt_align_justify" id={'politicaID'}>
        
                <p><strong>POLITICA DE TRATAMIENTO Y PROTECCION DE DATOS PERSONALES </strong>&nbsp;</p>
                <p>La presente Pol&iacute;tica de Tratamiento y Protecci&oacute;n de Datos Personales, se aplicar&aacute; a todas las Bases de Datos y/o Archivos que contengan los datos personales y que sean objeto de tratamiento por la Fundaci&oacute;n Internacional de Libre Expresi&oacute;n Motora y de Destrezas INTERLEM&rsquo;D (En adelante INTERLEM&rsquo;D), Considerado como responsable y/o encargado del tratamiento de los datos personales.</p>
                <p><strong>OBJETIVO: </strong></p>
                <p>Establecer los criterios para la recolecci&oacute;n, almacenamiento, uso, circulaci&oacute;n y supresi&oacute;n de los datos con la finalidad de dar la protecci&oacute;n necesaria utilizando herramientas y mecanismos que permitan dar garant&iacute;a para su confidencialidad, autenticidad y la integridad de la informaci&oacute;n tratada por INTERLEM&rsquo;D.</p>
                <p><strong>ALCANCE: </strong></p>
                <p>Esta pol&iacute;tica aplica para toda la informaci&oacute;n personal registrada en las bases de datos de la Fundaci&oacute;n Internacional de Libre Expresi&oacute;n Motora y de Destrezas INTERLEM&rsquo;D, quien act&uacute;a en calidad de responsable del tratamiento de los datos personales.</p>
                <p><strong>OBLIGACIONES:</strong></p>
                <p>Esta pol&iacute;tica es de obligatorio y estricto cumplimiento para la Fundaci&oacute;n Internacional de Libre Expresi&oacute;n Motora y de Destrezas INTERLEM&rsquo;D.</p>
                <p><strong>RESPONSABLE DEL TRATAMIENTO:</strong></p>
                <p>&nbsp;La Fundaci&oacute;n Internacional de Libre Expresi&oacute;n Motora y de Destrezas INTERLEM&rsquo;D, legalmente constituida, identificada con el NIT 900459129-7, con domicilio principal en la Carrera 24 No. 23-05. Barrio el Salitre - Yopal Casanare, Rep&uacute;blica de Colombia. P&aacute;gina www.interlemd.org</p>
                <p>Tel&eacute;fono 3142981372 - 3112520242 en la ciudad de Yopal &ndash; Casanare,</p>
                <p><strong>DEFINICIONES </strong></p>
                <p><strong>Dato personal</strong>: Se trata de cualquier informaci&oacute;n vinculada o que pueda asociarse a una persona determinada, como su nombre o n&uacute;mero de identificaci&oacute;n, o que puedan hacerla determinable, como sus rasgos f&iacute;sicos.</p>
                <p><strong>Dato p&uacute;blico</strong>: Es uno de los tipos de datos personales existentes. Son considerados datos p&uacute;blicos, entre otros, los datos relativos al estado civil de las personas, a su profesi&oacute;n u oficio y a su calidad de comerciante o de servidor p&uacute;blico. Por su naturaleza, los datos p&uacute;blicos pueden estar contenidos, entre otros, en registros p&uacute;blicos, documentos p&uacute;blicos, gacetas y boletines oficiales y sentencias judiciales debidamente ejecutoriadas que no est&eacute;n sometidas a reserva.</p>
                <p><strong>Dato semiprivado</strong>: Son los datos que no tienen naturaleza &iacute;ntima, reservada, ni p&uacute;blica y cuyo</p>
                <p>conocimiento o divulgaci&oacute;n puede interesar no solo al titular sino a cierto sector o a la sociedad en general. Los datos financieros y crediticios de la actividad comercial o de servicios, son algunos ejemplos.</p>
                <p><strong>Dato privado</strong>: Es el dato que por su naturaleza &iacute;ntima o reservada solo es relevante para el titular. Los gustos o preferencias de las personas, por ejemplo, corresponden a un dato privado.</p>
                <p><strong>Datos sensibles</strong>: Son aquellos que afectan la intimidad del titular o pueden dar lugar a que lo discriminen, es decir, aquellos que revelan su origen racial o &eacute;tnico, su orientaci&oacute;n pol&iacute;tica, las convicciones religiosas o filos&oacute;ficas, la pertenencia a sindicatos, organizaciones sociales, de derechos humanos, as&iacute; como los datos relativos a la salud, a la vida sexual, y los datos biom&eacute;tricos, entre otros.</p>
                <p><strong>Autorizaci&oacute;n</strong>: Es el consentimiento que da cualquier persona para que las empresas o personas responsables del tratamiento de la informaci&oacute;n, puedan utilizar sus datos personales.</p>
                <p><strong>Base de Datos</strong>: Conjunto organizado de datos personales que son objeto de tratamiento.</p>
                <p><strong>Encargado del tratamiento</strong>: Es la persona natural o jur&iacute;dica que realiza el tratamiento de datos personales, a partir de una delegaci&oacute;n que le hace el responsable, recibiendo instrucciones acerca de la forma en la que deber&aacute;n ser administrados los datos.&nbsp;&nbsp;</p>
                <p><strong>Responsable del tratamiento</strong>: Es la persona natural o jur&iacute;dica, p&uacute;blica o privada, que decide sobre la finalidad de las bases de datos y/o el tratamiento de los mismos.</p>
                <p><strong>Titular</strong>: Es la persona natural cuyos datos personales son objeto de tratamiento.</p>
                <p><strong>Tratamiento</strong>: Cualquier operaci&oacute;n o conjunto de operaciones sobre datos personales, tales como la recolecci&oacute;n, almacenamiento, uso, circulaci&oacute;n o supresi&oacute;n.</p>
                <p><strong>Aviso de privacidad</strong>: Es una de las opciones de comunicaci&oacute;n verbal o escrita que brinda la ley para darle a conocer a los titulares de la informaci&oacute;n, la existencia y las formas de acceder a las pol&iacute;ticas de tratamiento de la informaci&oacute;n y el objetivo de su recolecci&oacute;n y uso.</p>
                <p>Transferencia: Se trata de la operaci&oacute;n que realiza el responsable o el encargado del tratamiento de los datos personales, cuando env&iacute;a la informaci&oacute;n a otro receptor, que, a su vez, se convierte en responsable del tratamiento de esos datos.</p>
                <p><strong>TRATAMIENTO Y FINALIDAD:</strong></p>
                <p>El tratamiento que realizar&aacute; INTERLEM&rsquo;D con la informaci&oacute;n personal ser&aacute; el siguiente:</p>
                <p>La recolecci&oacute;n, almacenamiento, uso, circulaci&oacute;n, procesamiento de datos personales garantizando los lineamientos de seguridad y confidencialidad definidos en la Ley 1581 de 2012 y el Decreto 1377 de 2013, para:</p>
                <p>Registrar la informaci&oacute;n de datos personales en las bases de datos y sistemas de informaci&oacute;n de INTERLEM&rsquo;D.</p>
                <p>Ofrecer los programas corporativos de bienestar y planificar actividades institucionales, para el titular.</p>
                <p>Efectuar encuestas de satisfacci&oacute;n respecto de los bienes y servicios ofrecidos por INTERLEM&rsquo;D.</p>
                <p>Efectuar las gestiones pertinentes para el desarrollo del objeto social de la fundaci&oacute;n INTERLEM&rsquo;D en lo que tiene que ver con el cumplimiento de los acuerdos, convenios y contratos realizados con el Titular de la informaci&oacute;n.</p>
                <p><strong>DERECHOS DE LOS TITULARES:</strong></p>
                <p>Como titular de sus datos personales Usted tiene derecho a:</p>
                <p>(i)&nbsp;&nbsp; Acceder de forma gratuita a los datos proporcionados que hayan sido objeto de tratamiento.</p>
                <p>(ii)&nbsp; Conocer, actualizar y rectificar su informaci&oacute;n frente a datos parciales, inexactos, incompletos, fraccionados, que induzcan a error, o aquellos cuyo tratamiento est&eacute; prohibido o no haya sido autorizado.</p>
                <p>(iii)&nbsp; Solicitar prueba de la autorizaci&oacute;n otorgada.</p>
                <p>(iv)&nbsp; Presentar ante la Superintendencia de Industria y Comercio (SIC) quejas por infracciones a lo dispuesto en la normatividad vigente.</p>
                <p>(v)&nbsp; Revocar la autorizaci&oacute;n y/o solicitar la supresi&oacute;n del dato, siempre que no exista un deber legal o contractual que impida eliminarlos.</p>
                <p>(vi)&nbsp; Abstenerse de responder las preguntas sobre datos sensibles. Tendr&aacute; car&aacute;cter facultativo las respuestas que versen sobre datos sensibles o sobre datos de las ni&ntilde;as y ni&ntilde;os y adolescentes.</p>
                <p><strong>ATENCI&Oacute;N DE PETICIONES, CONSULTAS Y RECLAMOS </strong></p>
                <p>El &aacute;rea de Informaci&oacute;n de INTERLEM&rsquo;D es la dependencia que tiene a cargo dar tr&aacute;mite a las solicitudes de los titulares para hacer efectivos sus derechos.</p>
                <p>Domicilio principal en la Carrera 24 No. 23-05. Barrio el Salitre - Yopal Casanare, Rep&uacute;blica de Colombia. P&aacute;gina: https://www.facebook.com/ong.interlemd</p>
                <p>Tel&eacute;fono 3142981372 - 3112520242 en la ciudad de Yopal &ndash; Casanare.</p>
                <p>Correo: &nbsp;</p>
                <p><strong>PROCEDIMIENTO PARA EL EJERCICIO DEL DERECHO DE HABEAS DATA </strong></p>
                <p>En cumplimiento de las normas sobre protecci&oacute;n de datos personales, INTERLEM&rsquo;D</p>
                <p>presenta el procedimiento y requisitos m&iacute;nimos para el ejercicio de sus derechos:</p>
                <p>Para la radicaci&oacute;n y atenci&oacute;n de su solicitud le solicitamos suministrar la siguiente informaci&oacute;n:</p>
                <p>Nombre completo y apellidos</p>
                <p>Datos de contacto (Direcci&oacute;n f&iacute;sica y/o electr&oacute;nica y tel&eacute;fonos de contacto),</p>
                <p>Medios para recibir respuesta a su solicitud,</p>
                <p>Motivo(s)/hecho(s) que dan lugar al reclamo con una breve descripci&oacute;n del derecho que desea ejercer (conocer, actualizar, rectificar, solicitar prueba de la autorizaci&oacute;n otorgada, revocarla, suprimir, acceder a la informaci&oacute;n)</p>
                <p>Firma (si aplica) y n&uacute;mero de identificaci&oacute;n.</p>
                <p>El t&eacute;rmino m&aacute;ximo previsto por la ley para resolver su reclamaci&oacute;n es de quince (15) d&iacute;as h&aacute;biles, contado a partir del d&iacute;a siguiente a la fecha de su recibo. Cuando no fuere posible atender el reclamo dentro de dicho t&eacute;rmino, INTERLEM&rsquo;D informar&aacute; al interesado los motivos de la demora y la fecha en que se atender&aacute; su reclamo, la cual en ning&uacute;n caso podr&aacute; superar los ocho (8) d&iacute;as h&aacute;biles siguientes al vencimiento del primer t&eacute;rmino.</p>
                <p>Una vez cumplidos los t&eacute;rminos se&ntilde;alados por la Ley 1581 de 2012 y las dem&aacute;s normas que la reglamenten o complementen, el Titular al que se deniegue, total o parcialmente, el ejercicio de los derechos de acceso, actualizaci&oacute;n, rectificaci&oacute;n, supresi&oacute;n y revocaci&oacute;n, podr&aacute; poner su caso en conocimiento de la Superintendencia de Industria y Comercio &ndash;Delegatura para la Protecci&oacute;n de Datos Personales-.</p>  
                <p><strong>VIGENCIA: </strong></p>
                <p>La presente Pol&iacute;tica para el Tratamiento de Datos Personales rige a partir del 01 de enero de 2012.</p>
                <p>Las bases de datos en las que se registrar&aacute;n los datos personales tendr&aacute;n una vigencia igual al tiempo en que se mantenga y utilice la informaci&oacute;n para las finalidades descritas en esta pol&iacute;tica. Una vez se cumpla(n) esa(s) finalidad(es) y siempre que no exista un deber legal o contractual de conservar su informaci&oacute;n, sus datos ser&aacute;n eliminados de nuestras bases de datos.</p>
                <p>Los datos personales proporcionados se conservar&aacute;n mientras no se solicite su supresi&oacute;n por el interesado y siempre que no exista un deber legal de conservarlos.</p>
        </section>  

      </main>
      </EmptyLayout>
    );
  }
}

export default Terminos;
